/* Student Dashboard - Using Admin Theme */
@import url('../../admin/dashboard/dashboard.css');

/* Student-specific customizations while maintaining admin theme */
.dashboard-title {
  color: #5d4037 !important;
}

.dashboard-subtitle {
  color: #666 !important;
}

/* Ensure consistent spacing */
.dashboard-container {
  padding: 0;
  background: transparent;
}

/* Beautiful Course Card Improvements */
.course-card {
  height: 100% !important;
  display: flex !important;
  flex-direction: column !important;
  border-radius: 24px !important;
  overflow: hidden !important;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1) !important;
}

.course-card:hover {
  transform: translateY(-8px) !important;
  box-shadow: 0 20px 64px rgba(102, 126, 234, 0.15) !important;
}

.course-card .ant-card-body {
  padding: 0 !important;
  flex: 1 !important;
  display: flex !important;
  flex-direction: column !important;
}

.course-card .ant-card-meta {
  flex: 1 !important;
  display: flex !important;
  flex-direction: column !important;
}

.course-card .ant-card-meta-detail {
  flex: 1 !important;
  display: flex !important;
  flex-direction: column !important;
}

.course-card .ant-card-meta-description {
  flex: 1 !important;
}

/* Enhanced Search Input */
.ant-input-search .ant-input-group .ant-input {
  border-radius: 12px 0 0 12px !important;
  border: 2px solid #f0f0f0 !important;
  transition: all 0.3s ease !important;
}

.ant-input-search .ant-input-group .ant-input:focus {
  border-color: #667eea !important;
  box-shadow: 0 0 0 2px rgba(102, 126, 234, 0.1) !important;
}

/* Enhanced Tabs */
.ant-tabs-tab {
  border-radius: 16px !important;
  margin: 4px !important;
  transition: all 0.3s ease !important;
}

.ant-tabs-tab-active {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%) !important;
  color: white !important;
}

.ant-tabs-tab:hover {
  color: #667eea !important;
}

.ant-tabs-content-holder {
  padding: 0 !important;
}

/* Enhanced Select */
.ant-select-selector {
  border-radius: 12px !important;
  border: 2px solid #f0f0f0 !important;
  transition: all 0.3s ease !important;
}

.ant-select-focused .ant-select-selector {
  border-color: #667eea !important;
  box-shadow: 0 0 0 2px rgba(102, 126, 234, 0.1) !important;
}

/* Enhanced Progress Bar */
.ant-progress-bg {
  border-radius: 8px !important;
}

/* Button Hover Effects */
.ant-btn-primary {
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1) !important;
}

.ant-btn-primary:hover {
  transform: translateY(-2px) !important;
  box-shadow: 0 8px 24px rgba(102, 126, 234, 0.4) !important;
}

/* Course progress adjustments for student view */
.course-progress-item .course-meta {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

/* Activity list styling for certificates */
.activity-list {
  padding: 0;
}

.activity-item {
  border-bottom: 1px solid #f5f5f5;
  margin-bottom: 0;
  transition: all 0.2s ease;
}

.activity-item:last-child {
  border-bottom: none;
}

.activity-item:hover {
  background: rgba(93, 64, 55, 0.02);
  border-radius: 8px;
  margin: 0 -12px;
  padding-left: 12px !important;
  padding-right: 12px !important;
}

/* Profile card center alignment */
.content-card .ant-card-body {
  text-align: left;
}

/* Button styling consistency */
.dashboard-action-btn {
  background: #5d4037 !important;
  border-color: #5d4037 !important;
}

.dashboard-action-btn:hover {
  background: #4a2c20 !important;
  border-color: #4a2c20 !important;
}

.card-action-btn {
  color: #5d4037 !important;
}

.card-action-btn:hover {
  color: #4a2c20 !important;
}

/* Empty state styling */
.ant-empty-description {
  color: #666 !important;
}

/* Statistics cards hover effect */
.stat-card:hover {
  transform: translateY(-2px) !important;
  box-shadow: 0 8px 25px rgba(93, 64, 55, 0.12) !important;
}

/* Course grid improvements */
.ant-row {
  display: flex;
  flex-wrap: wrap;
}

.ant-col {
  display: flex;
}

/* Custom Animations */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.course-card {
  animation: fadeInUp 0.6s ease-out !important;
}

.course-card:nth-child(1) { animation-delay: 0.1s !important; }
.course-card:nth-child(2) { animation-delay: 0.2s !important; }
.course-card:nth-child(3) { animation-delay: 0.3s !important; }
.course-card:nth-child(4) { animation-delay: 0.4s !important; }
.course-card:nth-child(5) { animation-delay: 0.5s !important; }
.course-card:nth-child(6) { animation-delay: 0.6s !important; }

/* Ensure equal height cards */
@media (min-width: 992px) {
  .course-card {
    min-height: 600px;
  }
}

/* Responsive improvements */
@media (max-width: 768px) {
  .course-card .ant-card-body {
    padding: 16px !important;
  }
  
  .activity-item:hover {
    margin: 0 -8px;
    padding-left: 8px !important;
    padding-right: 8px !important;
  }

  .course-card {
    min-height: auto !important;
  }

  /* Mobile header adjustments */
  .dashboard-container > div:first-child {
    padding: 32px 20px !important;
  }

  .dashboard-container > div:first-child h1 {
    font-size: 28px !important;
  }

  .dashboard-container > div:first-child p {
    font-size: 16px !important;
  }
}

@media (max-width: 576px) {
  .course-card {
    min-height: 480px !important;
  }
  
  .dashboard-container > div:first-child {
    padding: 24px 16px !important;
  }

  .dashboard-container > div:first-child h1 {
    font-size: 24px !important;
  }
}
